<template>
  <div class="columns">
    <!--ÊñáÁ´†ËØ¶ÊÉÖ-->
    <div class="column is-three-quarters">
      <!--‰∏ªÈ¢ò-->
      <el-card
          class="box-card"
          shadow="never"
      >
        <div
            slot="header"
            class="has-text-centered"
        >
          <p class="is-size-5 has-text-weight-bold">{{ topic.title }}</p>
          <div class="has-text-grey is-size-7 mt-3">
            <span>{{ dayjs(topic.createTime).format('YYYY/MM/DD HH:mm:ss') }}</span>
            <el-divider direction="vertical"/>
            <span>ÂèëÂ∏ÉËÄÖÔºö{{ topicUser.alias }}</span>
            <el-divider direction="vertical"/>
            <span>Êü•ÁúãÔºö{{ topic.view }}</span>
          </div>
        </div>

        <!--Markdown-->
        <div id="preview"/>
        <div class="handle" >
          <i class="iconfont icon-dianzan1" :class="{active:handleList.like}" @click="handleLike('1',$event)">{{topic.like}}</i>
          <i class="iconfont icon-toubi" :class="{active:handleList.coin}" @click="handleLike('2',$event)">{{topic.coin}}</i>
          <i class="iconfont icon-shoucang" :class="{active:handleList.collects}" @click="handleLike('3',$event)">{{topic.collects}}</i>
        </div>
        <!--Ê†áÁ≠æ-->
        <nav class="level has-text-grey is-size-7 mt-6">
          <div class="level-left">
            <p class="level-item">
              <b-taglist>
                <router-link
                    v-for="(tag, index) in tags"
                    :key="index"
                    :to="{ name: 'tag', params: { name: tag.name } }"
                >
                  <b-tag type="is-info is-light mr-1">
                    {{ "#" + tag.name }}
                  </b-tag>
                </router-link>
              </b-taglist>
            </p>
          </div>
          <div
              v-if="token && user.id === topicUser.id"
              class="level-right"
          >
            <router-link
                class="level-item"
                :to="{name:'topic-edit',params: {id:topic.id}}"
            >
              <span class="tag">ÁºñËæë</span>
            </router-link>
            <a class="level-item">
              <span
                  class="tag"
                  @click="handleDelete(topic.id)"
              >Âà†Èô§</span>
            </a>
          </div>
        </nav>
      </el-card>

      <lv-comments :slug="topic.id"/>
    </div>

    <div class="column">
      <!--‰ΩúËÄÖ-->
      <Author
          v-if="flag"
          :user="topicUser"
      />
      <!--Êé®Ëçê-->
      <recommend
          v-if="flag"
          :topic-id="topic.id"
      />
    </div>
  </div>
</template>

<script>
import {deleteTopic, getTopic,handleList,getHandleList} from '@/api/post'
import {setRecommendList} from "../../utils/localStorage";
import {mapGetters} from 'vuex'
import Author from '@/views/post/Author'
import Recommend from '@/views/post/Recommend'
import LvComments from '@/components/Comment/Comments'
import Vditor from 'vditor'
import 'vditor/dist/index.css'

export default {
  name: 'TopicDetail',
  components: {Author, Recommend, LvComments},
  computed: {
    ...mapGetters([
      'token', 'user'
    ])
  },
  data() {
    return {
      flag: false,
      topic: {
        content: '',
        id: this.$route.params.id,
        like:0,
        coin:0,
        collects:0
      },
      tags: [],
      topicUser: {},
      handleList:{
        like:false,
        coin: false,
        collects: false
      }
    }
  },
  mounted() {
    this.fetchTopic()
    this.getHandleList()

  },
  methods: {
    renderMarkdown(md) {
      Vditor.preview(document.getElementById('preview'), md, {
        hljs: {style: 'github'}
      })
    },
    // ÂàùÂßãÂåñ
    async fetchTopic() {
      getTopic(this.$route.params.id).then(response => {
        const {data} = response
        document.title = data.topic.title

        this.topic = data.topic
        this.tags = data.tags
        this.topicUser = data.user
        // this.comments = data.comments
        this.renderMarkdown(this.topic.content)
        this.flag = true
        this.setRecommend(data.tags)
      })
    },
    getHandleList(){
      if (this.token!=null){
        getHandleList(this.$route.params.id).then(res=>{
          const { data } =res
          this.handleList.like=data.like;
          this.handleList.coin=data.coin;
          this.handleList.collects=data.collects;
        })
      }
    },
    handleDelete(id) {
      deleteTopic(id).then(value => {
        const {code, message} = value
        alert(message)

        if (code === 200) {
          setTimeout(() => {
            this.$router.push({path: '/'})
          }, 500)
        }
      })
    },
    handleLike(da,e){
      if (this.token==null){
        this.$router.push({
          path:"/login"
        })
        return
      }
      let flag=true;
      const a=e.target.classList.contains("active")
      if (a){
        flag=!flag
        if (da==="2"){
          return;
        }
      }
      const data={
        topicId:this.$route.params.id,
        handleType:da,
        done:flag
      }
      handleList(data).then(()=>{
        if (a){
            e.target.innerText=parseInt(e.target.innerText)-1
            e.target.classList.remove("active")
        }else {
          e.target.innerText=parseInt(e.target.innerText)+1
          e.target.classList.add("active")
        }
      }).catch(()=>{})
    },
    handleCoin(e){
      if (e.target.classList.contains("active")){
        this.topic.coin-=1
        this.handleList.coin=false
      }else {
        this.topic.coin+=1
        this.handleList.coin=true
      }
    },
    handleCollection(e){
      if (this.token==null){
        this.router.push({
          path:"/login"
        })
      }
      let flag=false;
      const a=e.target.classList.contains("active")
      if (a){
        this.topic.collects-=1
        this.handleList.collects=false
      }else {
        this.topic.collects+=1
        this.handleList.collects=true
        flag=!flag
      }

    },
   /* handle(e) {
      e.target.classList.add("active")
      switch (e.target.getAttribute("data-type")) {
        case "like":
          console.log("like")
          this.like+=1
          break;
        case "coin":
          console.log("coin")
          this.coin+=1
          break;
        case "collection":
          console.log("collection")
          this.collection+=1
          break;
      }
    }*/

    setRecommend(tags){
      for (let i=0;i<tags.length;i++){
        setRecommendList(this.tags[i].name)
      }
    }
  }
}
</script>

<style>
@import '//at.alicdn.com/t/font_2847596_p5p3df017r.css';

#preview {
  min-height: 300px;
}
.handle{
  text-align: center;
}
.iconfont {
  cursor: pointer;
  font-size: 25px !important;
  margin:0 20px;
  user-select: none;
}

.active{
  color: red;
}
</style>
