        <p>
          <strong>{{ comment.username }}</strong>
          <small class="ml-2">{{ comment.createTime | date }}</small>
          <br/>
          {{ comment.content }}
        </p>
        <div>
          <span class="controls">üëç19</span>
          <span class="controls">üëç0</span>
          <span class="controls" @click="reply" ref="reply">üí¨0</span>
        </div>
      </div>
      <LvCommentsForm v-if="flag" :show="comment"
                      :slug="slug"
                      ref="repl"></LvCommentsForm>
      <div v-for="(item,i) in comment.replies" :key="i">
        <div class="media reply" >
          <figure class="media-left image is-48x48">
            <el-image :src="`https://cn.gravatar.com/avatar/${comment.userId}?s=164&d=monsterid`">
              <div slot="error" class="image-slot">
                <i class="el-icon-picture-outline"></i>
              </div>
            </el-image>
<!--            <img :src="`https://cn.gravatar.com/avatar/${comment.userId}?s=164&d=monsterid`">-->
          </figure>
          <div class="content">
            <p>
              <strong>{{item.username }}</strong>
              <small class="ml-2">{{item.createTime | date }}</small>
              <br/>
              {{ item.content }}
            </p>
            <div>
              <span class="controls">üëç19</span>
              <span class="controls">üëç0</span>
              <span class="controls" @click="reply" ref="reply">üí¨0</span>
            </div>
          </div>
        </div>
<!--        <LvCommentsForm :show="comment"
                        :slug="slug"
                        ref="repl"></LvCommentsForm>-->
      </div>

    </div>
  </article>
</template>

<script>

import LvCommentsForm from "./CommentsForm";

export default {
  name: 'LvCommentsItem',
  components: {LvCommentsForm},
  data() {
    return {
      flag: false
    }
  },
  props: {
    comment: {
      type: Object,
      required: true
    },
    slug: {
      type: String,
      default: null
    },
  },
  mounted() {
    this.$bus.$on('de', () => {
      if (this.flag) {
        this.flag = false;
      }
    })
    // this.closeReply()
  },
  methods: {
    reply(e) {
      /*console.log(e)
      var para = document.createElement("h1");
      var node = document.createTextNode("ËøôÊòØ‰∏Ä‰∏™Êñ∞ÁöÑÊÆµËêΩ„ÄÇ");
      para.appendChild(node);
      e.target.parentNode.parentNode.parentNode.parentNode.appendChild(para)*/
      if (!this.flag) {
        this.$bus.$emit('de')
      }
      this.flag = !this.flag;
    },
    closeReply(){
      const _this = this;
      const d = document.getElementsByClassName("repl")
      document.addEventListener('click', function (e) {
        if (d.length > 1) {
          if (!d[1].contains(e.target)&&!_this.$refs.reply.contains(e.target)) {
            _this.flag = false;
          }
        }else {
          if (!_this.$refs.reply.contains(e.target)) {
            _this.flag = false;
          }
        }
      })
    }
  },
  beforeDestroy() {
    this.$bus.$off('de')
  }
}
</script>

