
export function getRecommends() {
    const arr = JSON.parse(localStorage.getItem("recommendList"))||[]
    function compare(arg) {
        return function(a, b) {
            return b[arg] - a[arg];
        }
    }
    arr.sort(compare('count'))
    return arr.slice(0,5)
}
export function setRecommendList(tag) {
    let recommendList = getRecommends() || []
    if (recommendList.length>0){
        for (let i=0;i<recommendList.length;i++){
            if (recommendList[i].name===tag){
                recommendList[i].count+=1
                localStorage.setItem("recommendList", JSON.stringify(recommendList))
                return;
            }
        }
    }
    recommendList.push({name:tag,count:1})
    localStorage.setItem("recommendList", JSON.stringify(recommendList))
}

export function getUser() {
    return JSON.parse(localStorage.getItem("u_user"))
}

export function setUser(user) {
    localStorage.setItem("u_user", JSON.stringify(user))
}
export function clearUser(){
    localStorage.removeItem("u_user")
}

export function getFriends(number) {
    return JSON.parse(localStorage.getItem(number))
}

export function setFriends(number, friend) {
    const friends = getFriends(number);  /*||[]*/
    let friendList = [];
    if (friend.number===number){
        return
    }
    if (friends != null) {
        if (friends.length > 0) {
            friendList = friendList.concat(friends)
            console.log(friendList)
            for (var i=0;i<friendList.length;i++){
                if (friendList[i].number===friend.number){
                    return
                }
            }
        }
    }
    friendList.push(friend)
    localStorage.setItem(number, JSON.stringify(friendList))
}

export function clearFriend(number,friend){
    const friends = getFriends(number)||[];
    if (friends.length>0){
        friends.splice(friends.indexOf(friend),1)
        localStorage.setItem(number,JSON.stringify(friends))
    }
}